      },
    }
  },
   mounted(){

    var router = this.$route.name;
    if (router == "Home") {
      this.activatedStatus.Home = "active"
    }
    if (router == "activity") {
      this.activatedStatus.Activity = "active"
    }
    if (router == "activity.single") {
