import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '@/views/Home.vue'
import Blog from '@/views/blog/Blog.vue'
import BlogSingle from '@/views/blog/BlogSingle.vue'
import Contact from '@/views/Contact.vue'
import Activity from '@/views/activity/Activity.vue'
import ActivitySingle from '@/views/activity/ActivitySingle.vue'
import DakwahPeribadatan from '@/views/service/DakwahPeribadatan.vue'
import Ekonomi from '@/views/service/Ekonomi.vue'
import Fundraising from '@/views/service/Fundraising.vue'
import Pendidikan from '@/views/service/Pendidikan.vue'
import SaranaPrasarana from '@/views/service/SaranaPrasarana.vue'
import SosialMultimedia from '@/views/service/SosialMultimedia.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/about',
    name: 'About',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/About.vue')
  },{
    path: '/blog',
    name: 'blog',
    component: Blog,
  },{
    path: '/blog/blogsingle',
    name: 'blog.single',
    component: BlogSingle,
  }
  ,{
    path: '/contact',
    name: 'contact',
    component: Contact,
  },{
    path: '/activity',
    name: 'activity',
    component: Activity,
  },{
    path: '/activity/activitysingle',
    name: 'activity.single',
    component: ActivitySingle,
  },{
    path: '/service/dakwahperibadatan',
    name: 'dakwahperibadatan',
    component: DakwahPeribadatan,
  },{
    path: '/service/ekonomi',
    name: 'ekonomi',
    component: Ekonomi,
  },{
    path: '/service/fundraising',
    name: 'fundraising',
    component: Fundraising,
  },{
    path: '/service/pendidikan',
    name: 'pendidikan',
    component: Pendidikan,
  },{
    path: '/service/saranaprasarana',
    name: 'saranaprasarana',
    component: SaranaPrasarana,
  },{
    path: '/service/sosialmultimedia',
    name: 'sosialmultimedia',
    component: SosialMultimedia ,
  }
]

const router = new VueRouter({
  mode: 'history',
  routes,
  // scrollBehavior(to, from, savedPosition) {
  //   return { x: 0, y: 0 };
  // }
  scrollBehavior() {
    return {x: 0, y: 0}
  }
})

export default router
